import React, {Fragment, Component} from 'react';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'; 
import './App.css';
import Navbar from './components/layout/Navbar';
import Users from './components/users/Users';
import User from './components/users/User';
import axios from 'axios'; 
import Search from './components/users/Search';
import { render } from '@testing-library/react';
import About from './components/pages/About'
import Alert from './components/layout/Alert'


class App extends Component {
  state = {
    users: [],
    user: {},
    repos:[],
    loading: false,
    alert:null
  }

  // async componentDidMount() {
  //   this.setState({loading:true}); 
  //   const res = await axios.get(`https://api.github.com/users?client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&
  //   ${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`);
  //   this.setState({users:res.data, loading:false});
  // }
  //Search Github Users
  searchUsers = async text => {
    this.setState({loading:true}); 
    const res = await axios.get(`https://api.github.com/search/users?q=${text}&client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=
    ${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`);
    this.setState({users:res.data.items, loading:false});
  }

  //Get Single User
  getUser = async (username) => {

    this.setState({loading:true}); 
    const res = await axios.get(`https://api.github.com/users/${username}?client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=
    ${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`);
    this.setState({user: res.data, loading:false});
    console.log(res.data);

  }
//Get Single Repos
getUserRepos = async (username) => {

  this.setState({loading:true}); 
  const res = await axios.get(`https://api.github.com/users/${username}/repos?per_page=5&sort=created:asc&client_id=${process.env.REACT_APP_GITHUB_CLIENT_ID}&client_secret=
  ${process.env.REACT_APP_GITHUB_CLIENT_SECRET}`);
  this.setState({repos: res.data, loading:false});
  console.log(res.data);

}

  clearUsers = () => {
    this.setState({users:[], loading:false});
  }

  //Set alert
  setAlert = (msg, type) => {
    this.setState({alert:{msg: msg, type: type}});
    setTimeout(() => this.setState({alert : null}), 1500);
  }
  render()
  {   
    const {users, user, repos, loading} = this.state;
    return (
      <div className="App">   
        <Router>
        <Navbar />  
        <div className="container">
        <Alert alert = {this.state.alert}/>
        <Switch>
          <Route exact path = "/" render = {props => (<Fragment>
        <Search searchUsers = {this.searchUsers} clearUsers = {this.clearUsers} showClear = {users.length > 0 ? true : false} 
        setAlert = {this.setAlert} > </Search>
        <Users loading = {this.state.loading} users = {users}></Users>
        
        </Fragment>)}/>
        <Route exact path = "/About" component = {About}></Route>
        <Route exact path = "/user/:login" render = {props => (
        <User { ...props} getUser = {this.getUser} user = {user} getUserRepos = {this.getUserRepos} repos = {repos}loading = {loading}>

        </User> )}></Route>
        </Switch>
        </div>
        </Router>
      </div>
    );
  }
};

export default App; 